import React from 'react';
import styles from './Habit.scss';
import QA from 'qa';
import { Router } from 'react-router';

const questions = [
  {
    title: '',
    answer: [
      'Leader(P7P8)',
      '',
      '',
      ''
    ]
  },
  {
    title: '',
    answer: [
      '',
      '',
      ''
    ]
  },
  {
    title: '&&',
    answer: [
      '',
      '@'
    ]
  },
  {
    title: '',
    answer: [
      '',
      '',
      '10'
    ]
  }, {
    title: '&&',
    answer: [
      '',
      ''
    ]
  },
  {
    title: ' &',
    answer: [
      ''
    ]
  },
  {
    title: '',
    answer: [
      '10'
    ]
  }
];

export default class AppContainer extends React.Component {
  state = {
    answers: []
  }

  showAnswer = qa => {
    this.setState({
      answers: [...this.state.answers, qa.answer]
    }, function callback(){
      console.log(this.state.answers)
    });
  }

  showAnswerByTitle = title => {
    let qa = questions.find(question => question.title === title);
    if(qa) {
      this.setState({
        answers: [...this.state.answers, qa.answer]
      }, function callback(){
        console.log(this.state.answers)
      });
    }
  }

  onAnwserClick = (title) => {
    if(title.indexOf('@') !== -1) {
      this.showAnswerByTitle(title.slice(1));
    }
  }

  render() {
    const { answers } = this.state;

    return (
      <div>
            <div className={styles.qaList}>
              {questions.map(qa => {
                return <QA onClick={this.showAnswer} qa={qa} />;
              })}
            </div>
            {answers.map(answer => {
              return <ul className={styles.answerList}>
                { answer.map(a => <li onClick={()=>{this.onAnwserClick(a)} }>{a}</li>)}
              </ul>;
            })}
      </div>
    );
  }
}
